/* Card Component START */

.card {
  .inner {
    background: white;
    box-shadow: $shadow;
    margin: 10px 10px;
    overflow: hidden;
    border-radius: 5px;
    .upper {
      height: 210px;
      position: relative;
      z-index: 1;
      color: white;
      margin-bottom: 10px;
      .image {
        background: url('https://turbloknot.com/img/hotel/12/amara-dolce-vita-1447259201.jpg')  50% 50% no-repeat;
        background-size: cover;
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 2;
        &:after {
          background: linear-gradient(180deg, rgba(28,44,73,0) 0%, #1C2C49 100%);
          content: '';
          width: 100%;
          height: 100%;
          bottom: 0;
          z-index: 3;
          position: absolute;
        }
      }
      .info {
        position: absolute;
        width: 100%;
        bottom: 0;
        z-index: 4;
        padding: 24px 28px;
        h2 {
          font-size: 20px;
          font-weight: 600;
          margin-bottom: 10px;
        }
        ul.features {
          list-style-type: none;
          font-size: 12px;
          li {
            display: inline-block;
            margin: 0 10px;
            &:first-child {
              margin-left: 0px;
            }
            &:last-child {
              margin-right: 0px;
            }
          }
        }
      }
    }
    .lower {
      display: flex;
      margin-bottom: 10px;
      .col {
        flex: auto;
        padding: 10px 20px;
        border-right: 1px solid rgba(0, 0, 0, 0.1);;
        text-align: left;
        &:last-child {
          border-right: none;
        }
        .price {
          font-size: 16px;
          font-weight: 600;
          span {
            font-weight: 400;
            font-size: 14;
          }
        }
        img {
          max-height: 18px;
          height: 100%;
          max-width: 102px;
          width: 100%;
          display: block;
        }
      }
    }
    .link {
      padding: 10px 20px;
      font-size: 14px;
      margin-bottom: 20px;
      a {
        padding: 10px 15px;
        background: $main;
        color: white;
        font-weight: 600;
        transition: all 0.3s;
        text-decoration: none;
        border-radius: 5px;
        margin-right: 20px;
        &:hover {
          background: $main-hover;
          box-shadow: inset 0px 1px 3px 0px #0000004d;
        }
      }
    }
  }
}
