@import url(https://fonts.googleapis.com/css?family=Roboto:100,100i,300,300i,400,400i,500,500i,700,700i,900,900i&subset=latin-ext);
/* Base */
/* Responsive breakpoints */
/* Reset START */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* Fonts START */
body {
  font-family: 'Roboto';
  line-height: 1.3; }

/* Columns START */
.row {
  display: flex;
  flex-wrap: wrap; }
  .row:after {
    content: '';
    clear: both;
    display: table; }

.col-1 {
  width: 8.33333%;
  float: left; }

.col-2 {
  width: 16.66667%;
  float: left; }

.col-3 {
  width: 25%;
  float: left; }

.col-4 {
  width: 33.33333%;
  float: left; }

.col-5 {
  width: 41.66667%;
  float: left; }

.col-6 {
  width: 50%;
  float: left; }

.col-7 {
  width: 58.33333%;
  float: left; }

.col-8 {
  width: 66.66667%;
  float: left; }

.col-9 {
  width: 75%;
  float: left; }

.col-10 {
  width: 83.33333%;
  float: left; }

.col-11 {
  width: 91.66667%;
  float: left; }

.col-12 {
  width: 100%;
  float: left; }

@media screen and (max-width: 650px) {
  .col-6 {
    width: 100%;
    float: left; } }

/* Global START */
body {
  background: #F6F6F6; }

.wrapper {
  max-width: 960px;
  width: 100%;
  margin: 0 auto;
  height: 100%;
  padding: 0 20px; }

.container {
  margin: 0 -10px; }

/* Components */
/* Header Component START */
header {
  width: 100%;
  height: 60px;
  background: white;
  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.06);
  margin-bottom: 32px; }
  header .header {
    display: flex;
    align-items: center; }
  header .logo {
    background: url(/travelbro/static/media/logo.0f2cf7a6.svg);
    width: 139px;
    height: 34px;
    display: block; }

/* Title Component START */
.title {
  text-align: center;
  margin-bottom: 32px; }
  .title span {
    font-size: 14px;
    font-weight: 400; }
  .title h1 {
    font-size: 24px;
    font-weight: 600; }

.results {
  position: relative; }

/* Card Component START */
.card .inner {
  background: white;
  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.06);
  margin: 10px 10px;
  overflow: hidden;
  border-radius: 5px; }
  .card .inner .upper {
    height: 210px;
    position: relative;
    z-index: 1;
    color: white;
    margin-bottom: 10px; }
    .card .inner .upper .image {
      background: url("https://turbloknot.com/img/hotel/12/amara-dolce-vita-1447259201.jpg") 50% 50% no-repeat;
      background-size: cover;
      position: absolute;
      width: 100%;
      height: 100%;
      z-index: 2; }
      .card .inner .upper .image:after {
        background: linear-gradient(180deg, rgba(28, 44, 73, 0) 0%, #1C2C49 100%);
        content: '';
        width: 100%;
        height: 100%;
        bottom: 0;
        z-index: 3;
        position: absolute; }
    .card .inner .upper .info {
      position: absolute;
      width: 100%;
      bottom: 0;
      z-index: 4;
      padding: 24px 28px; }
      .card .inner .upper .info h2 {
        font-size: 20px;
        font-weight: 600;
        margin-bottom: 10px; }
      .card .inner .upper .info ul.features {
        list-style-type: none;
        font-size: 12px; }
        .card .inner .upper .info ul.features li {
          display: inline-block;
          margin: 0 10px; }
          .card .inner .upper .info ul.features li:first-child {
            margin-left: 0px; }
          .card .inner .upper .info ul.features li:last-child {
            margin-right: 0px; }
  .card .inner .lower {
    display: flex;
    margin-bottom: 10px; }
    .card .inner .lower .col {
      flex: auto;
      padding: 10px 20px;
      border-right: 1px solid rgba(0, 0, 0, 0.1);
      text-align: left; }
      .card .inner .lower .col:last-child {
        border-right: none; }
      .card .inner .lower .col .price {
        font-size: 16px;
        font-weight: 600; }
        .card .inner .lower .col .price span {
          font-weight: 400;
          font-size: 14; }
      .card .inner .lower .col img {
        max-height: 18px;
        height: 100%;
        max-width: 102px;
        width: 100%;
        display: block; }
  .card .inner .link {
    padding: 10px 20px;
    font-size: 14px;
    margin-bottom: 20px; }
    .card .inner .link a {
      padding: 10px 15px;
      background: #FE13C2;
      color: white;
      font-weight: 600;
      transition: all 0.3s;
      text-decoration: none;
      border-radius: 5px;
      margin-right: 20px; }
      .card .inner .link a:hover {
        background: #c30091;
        box-shadow: inset 0px 1px 3px 0px #0000004d; }

.filter {
  position: absolute;
  left: -190px;
  width: 190px; }
  .filter .group {
    padding-bottom: 15px;
    border-bottom: 1px dashed rgba(32, 40, 64, 0.1);
    padding-top: 15px; }
    .filter .group:last-child {
      padding-bottom: 0;
      border-bottom: none; }
    .filter .group .title {
      font-size: 12px;
      color: #4A4A4A;
      text-transform: uppercase;
      font-weight: 600;
      margin-bottom: 10px;
      display: inline-block; }

/* Form Components */
.checkbox {
  margin-bottom: 10px; }

.checkbox input[type="checkbox"] {
  opacity: 0;
  display: none; }

.checkbox label {
  position: relative;
  display: inline-block;
  /*16px width of fake checkbox + 6px distance between fake checkbox and text*/
  padding-left: 22px;
  margin-left: -22px;
  font-size: 14px;
  color: #4A4A4A; }

.checkbox label::before,
.checkbox label::after {
  position: absolute;
  content: "";
  /*Needed for the line-height to take effect*/
  display: inline-block; }

/*Outer box of the fake checkbox*/
.checkbox label::before {
  height: 16px;
  width: 16px;
  left: 0px;
  /*(24px line-height - 16px height of fake checkbox) / 2 - 1px for the border
     *to vertically center it.
     */ }

/*Checkmark of the fake checkbox*/
.checkbox label::after {
  height: 4px;
  width: 10px;
  border-left: 2px solid #FE13C2;
  border-bottom: 2px solid #FE13C2;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
  left: 4px;
  top: 4px; }

/*Hide the checkmark by default*/
.checkbox input[type="checkbox"] + label::after {
  content: none; }

/*Unhide on the checked state*/
.checkbox input[type="checkbox"]:checked + label::after {
  content: ""; }

/*Adding focus styles on the outer-box of the fake checkbox*/
.checkbox input[type="checkbox"]:focus + label::before {
  outline: #3b99fc auto 5px; }

.checkbox input[type="checkbox"]:checked + label {
  color: #FE13C2;
  font-weight: 600;
  margin-left: -4px; }

/* Responsive */
/* Responsive START*/
@media screen and (max-width: 650px) {
  .container {
    margin: 0; }
  .card .inner {
    margin: 10px 0px; } }

