/* Columns START */
.row {
  display: flex;
  flex-wrap: wrap;
  &:after {
    content: '';
    clear:both;
    display: table;
  }
}
.col-1 {
  width:(100% / 12);
  float: left;
}
.col-2 {
  width:(100% / 6);
  float: left;
}
.col-3 {
  width:(100% / 4);
  float: left;
}
.col-4 {
  width:(100% / 3);
  float: left;
}
.col-5 {
  width:(100% / 2.4);
  float: left;
}
.col-6 {
  width:(100% / 2);
  float: left;
}
.col-7 {
  width:(100% / 1.714285714285714);
  float: left;
}
.col-8 {
  width:(100% / 1.5);
  float: left;
}
.col-9 {
  width:(100% / 1.333333333333333);
  float: left;
}
.col-10 {
  width:(100% / 1.2);
  float: left;
}
.col-11 {
  width:(100% / 1.090909090909091);
  float: left;
}
.col-12 {
  width:100%;
  float: left;
}

@media screen and (max-width: $tablet) {
  .col-6 {
    width: 100%;
    float: left;
  }
}
