.filter {
  position: absolute;
  left: -190px;
  width: 190px;
  .group {
    padding-bottom: 15px;
    border-bottom: 1px dashed rgba(32, 40, 64, 0.1);
    padding-top: 15px;
    &:last-child {
      padding-bottom: 0;
      border-bottom: none;
    }
    .title {
      font-size: 12px;
      color: #4A4A4A;
      text-transform: uppercase;
      font-weight: 600;
      margin-bottom: 10px;
      display: inline-block;
    }
  }
}
